import { Component } from "react";
import axios from "axios";

let endpoint = "https://yerba-mate-home.herokuapp.com";

export default class DatabasePage extends Component {
  state = {
    items: []
  }

  componentDidMount() {
    this.getAllMate();
  }

  // Getting all the mate from DB
  getAllMate = () => {
    axios.get(endpoint + "/api/mate")
      .then((response) => {
        if(response.data) {
          this.setState({
            items: response.data.map((item) => {
              return(
                <li className="database--li" key={item.slug}>
                  {item.name} - {item.country}<br />
                </li>
              );
            })
          });
        }
        else {
          this.setState({
            items: []
          });
        }
      });
  }

  render() {
    return(
      <div className="center-div">
        <h1 className="database--title">YERBA MATE</h1>
        <ul>
          {this.state.items.length == 0 ? "Loading..." : this.state.items}
        </ul>
      </div>
    );
  }
}
